import { lazy, Suspense } from "react";
import { useSelector, useDispatch } from "react-redux";
// import { Navigate } from "react-router-dom";
// import BottomNav from "../../pageLayout/BottomNav";
// import TopBar from "../../pageLayout/TopBar";
import { setRoute } from "../../../redux/actions/route";
const BottomNav = lazy(() => import("../../pageLayout/BottomNav"));
const TopBar = lazy(() => import("../../pageLayout/TopBar"));

export default function PrivateRoute({ children }) {
  // const logged = useSelector((state) => state.auth.isLoggedIn);
  const logged = useSelector((state) => state.auth.isLoggedIn);
  const dispatch = useDispatch();
  const navigateTo = (payload) => {
    const navigateToSome = () => {
      dispatch(setRoute(payload));
    };
    setTimeout(navigateToSome, 10);
  };

  if (logged) {
    return (
      <div className="h-screen flex flex-col">
        <div className="min-h-[8vh] overflow-hidden">
          <Suspense fallback={<div></div>}>
            <TopBar />
          </Suspense>
        </div>
        <div className="flex-grow overflow-y-auto">{children}</div>
        <div className="min-h-[8vh] overflow-hidden">
          <Suspense fallback={<div></div>}>
            <BottomNav />
          </Suspense>
        </div>
      </div>
    );
  } else {
    return <div onLoad={navigateTo("auth/login")}></div>;
  }
}
